*,
*::before,
*::after {
  box-sizing: border-box;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul[class],
ol[class] {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

button:focus {
  outline: none;
}

main.main {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  max-width: 1920px;
  overflow: hidden;
  height: auto;
}

.youtube, .searching, .phones, .music, .instagram, .facebook, .clock, .cart, .arows-right, .arows-acent-left, .arow-bottom {
  background: url("sprite.svg") no-repeat;
}

