header.header {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  max-width: 1920px;
  height: 118px;
}
header.header img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 420px;
}

section.content {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
}

footer.footer {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
}

@font-face {
  font-family: "Alice Regular";
  src: url("../fonts/Alice Regular.woff2") format("woff2"), url("../Alice Regular.woff") format("woff");
  font-weight: 400;
